<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >

          <!-- 请选择景区 start -->
         <el-form-item label="商店名称" prop="scenicSpotId">
         				<el-select
         					v-model="searchValidateForm.shopId"
         					placeholder="请选择商店"
         					filterable
         					clearable
         					style="width: 100%"
         				>
         					<el-option
         						v-for="item in shopList"
         						:key="item.shopId"
         						:label="item.shopName"
         						:value="item.shopId"
         					/>
         				</el-select>
         </el-form-item>
          <!-- 请选择景区 end -->

        </el-form>
        <!-- 搜索表单 end -->

        <!-- 查询数据 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 查询数据 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->
		
		
		<!-- 导出 start -->
		<el-button
		  type="primary"
		  :loading="downloadLoading"
		  icon="el-icon-top-right"
		  @click="exportExcel"
		  >导出数据</el-button
		>
		<!-- 导出 end -->
<!-- 		<el-button
		     type="primary"
		     icon="el-icon-circle-plus-outline"
		     @click="onAdd"
		     >新增</el-button
		   > -->
      </el-header>

      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
 <!--         <el-table-column label="操作" min-width="80">
            <template slot-scope="scope">
              <el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="primary" size="small">
                编辑
              </el-button>
			  <el-button
			    size="small"
			    type="danger"
			    @click.native.prevent="deleteRow(scope.$index, scope.row)"
			    >删除</el-button
			  >
            </template>
          </el-table-column> -->
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>

      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>
	  <el-dialog
	    class="dialog"
		width="25%"
	    :title="drawerTitle + '商店'"
	    :visible.sync="drawer"
	    :before-close="handleClose"
	    :close-on-click-modal="false"
	  >
	      <el-form
	        ref="ruleFormRef"
	        :model="validateForm"
	        label-width="100px"
	        class="demo-ruleForm"
	        :size="formSize"
	        status-icon
			style="width:100%"
	      >
			  <el-form-item label="商店名称" prop="scenicSpotId">
			  				<el-select
			  					v-model="validateForm.shopId"
			  					placeholder="请选择商店"
			  					filterable
			  					clearable
			  					style="width: 100%"
			  				>
			  					<el-option
			  						v-for="item in shopList"
			  						:key="item.shopId"
			  						:label="item.shopName"
			  						:value="item.shopId"
			  					/>
			  				</el-select>
			  </el-form-item>
	      	<el-form-item label="用户名称" prop="shopUserName">
	      	  <el-input v-model="validateForm.shopUserName" placeholder="请输入用户名称" />
	      	</el-form-item>
			
			<el-form-item label="用户手机号" prop="userPhone">
			  <el-input v-model="validateForm.userPhone" placeholder="请输入用户手机号" />
			</el-form-item>
			
			<el-form-item label="用户角色" prop="userRole">
			  <el-select
			  	v-model="validateForm.userRole"
			  	placeholder="请选择角色"
			  	filterable
			  	clearable
			  	style="width: 100%"
			  >
			  	<el-option
			  		v-for="item in userRoleList"
			  		:key="item.value"
			  		:label="item.label"
			  		:value="item.value"
			  	/>
			  </el-select>
			</el-form-item>
	      </el-form>
		  <div class="submit">
		    <el-button type="primary" @click="submitForm('validateForm')"
		      >提交</el-button
		    >
		    <el-button @click="cancelClick">取消</el-button>
		  </div>
	</el-dialog>
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { createUrl,formatDate } from '../utils/utils.js'
export default {
  name: 'SettlementFlow',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      userRoleList: [
        {
          value: '0',
          label: '暂无角色'
        },
        {
          value: '1',
          label: '商家'
        },
		{
		  value: '2',
		  label: '店员'
		}
      ], // 入库类型选项

      dateStateOptions: [
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按日查询'
        },
        {
          value: '3',
          label: '按年查询'
        }
      ], // 日期统计方式选项



      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd', // 日期格式

      searchValidateForm: {}, // 搜索表单验证对象



      downloadUrl: '',

      total: 0,



      // 搜索表单规则
      searchValidateFormRules: {},

      // 提交表单规则
      validateFormRules: {
        // // 机器人数量
        // robotCount: [
        //   { validator: checkNumberMethod('机器人数量'), trigger: ['blur', 'change'] }
        // ],
      },

	  //新增
	  showTable: true, // 是否显示表格
	    tableFields: {
	  		  景区名称:'scenicSpotName',
			  商店名称:'shopName',
	  		  商品名称:'exchangeName',
			  核销人:'writeOffUser',
			  核销用户手机号:'writeOffUserPhone',
			  核销时间:'writeOffDate',
			  创建时间:'createDate',
			  更新时间:'updateDate'
	      },
	      tableData: [], // 表格数据
	      tableLoading: false,
	  spotList:[],
	  shopList:[],
	  drawer:false,
	  direction:'rtl',
	  drawerTitle:'',
	  formSize:'',
	  validateForm: {}, // 表单验证对象
	  imgBaseUrl:'https://jxzy-robot.oss-cn-beijing.aliyuncs.com/'
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.getSearchValidateForm()
    this.getValidateForm()
	// this.getSpotList()
	this.getShopList()
    this.initTableData()
  },
  beforeMount() {},
  mounted() {},
  methods: {
	  // 点击删除时触发
	  deleteRow(index, row) {
	    this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
	      confirmButtonText: '确定',
	      cancelButtonText: '取消',
	      type: 'warning'
	    })
	      .then(async () => {

	        const { data: res } = await this.$http.get(
	          '/shopUser/deleteShopUser',
	          {
	            shopId: row.shopId
	          }
	        )
	        if (res.state == '200') {
	          this.$message({
	            type: 'success',
	            message: res.msg,
	            offset: 100,
	            center: true,
	            duration: 1000
	          })
	          this.initTableData()
	        } else {
	          this.$message({
	            type: 'error',
	            message: res.msg,
	            offset: 100,
	            center: true,
	            duration: 3000
	          })
	        }
	      })
	      .catch(() => {
	        this.$message({
	          type: 'info',
	          message: '已取消删除',
	          offset: 100,
	          center: true,
	          duration: 1000
	        })
	      })
	  },
	  cancelClick(){
		  this.drawer = false
		  this.validateForm = {}
	  },
	  // async getSpotList(){
	  // 		  const { data: res } = await this.$http.get('/scenicSpot/queryScenicSpotList')
	  // 		  if(res.state == 200){
	  // 			  this.spotList = res.data
	  // 		  }
	  // 		  console.log(res)
	  // },
	  async getShopList(){
	  	const { data: res } = await this.$http.post('/Merchant/sysShop/getShopList')
	  	if(res.state == 200){
	  		this.shopList = res.data.list
	  	}
	  	console.log(res)
	  },
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
      }
    },

    // 获取提交表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=houjingchen&password=houjingchen'
      )
    },

    /**
     * 初始化表格数据
     * @params - isPage - 是否分页
     */
    async initTableData(isPage = false) {
      this.tableLoading = true
	  
	const { data: res } = await this.$http.post(
        '/Merchant/shopWriteOffLog/getShopWriteOffLogList',{
			...this.searchValidateForm,
			pageNum: this.currentPage, // 第几页
			pageSize: this.pageSize // 每页多少条
		})
      console.log(res.data);
      if (res.state == '200') {
		this.tableData = res.data.list
		this.total = res.data.total
        this.tableLoading = false
      }
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
	  
	  const params = new URLSearchParams() // 创建对象
	  		params.append('shopId', this.searchValidateForm.shopId || '') // 封装对象
      const {data: res} = await this.$http.post(
      	'/Merchant/shopWriteOffLog/exportShopWriteOffLog', params,{
			responseType :'blob'
		}
      )
      if (res) {
      	const url = window.URL.createObjectURL(
      		new Blob([res], {
      			type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
      		})
      	)
      	const a = document.createElement('a')
      	console.log(url)
      	a.href = url
      	a.download = '核销日志'
      	a.click()
      	this.$message({
      		message: '导出成功',
      		type: 'success'
      	})
      } else {
      	this.$message({
      		message: '导入失败',
      		type: 'error'
      	})
      }
    },

    // 导入EXCEL
    async importE(file) {
      this.importLoading = true
      var formData = new FormData()
      formData.append('file', file)

      const { data: res } = await this.$http.post(
        '/system/inventory_detail/importExcel',
        formData
      )
      this.importLoading = false
      if (res.state == '200') {
        this.$message({
          message: '导入成功！',
          type: 'success'
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error'
        })
      }
    },




    // 重置
    onReset() {
      // 清空所有输入框内容
      this.getSearchValidateForm()
      this.searchStartEndDate = []
      this.selectDateState = '1'
      this.dateType = 'monthrange'

      // 重新获取表格数据
      this.initTableData()
    },

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true
          // console.log(this.searchValidateForm);
          this.initTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },


    // 新增
    onAdd() {
		this.drawer = true
		this.drawerTitle = '添加'
		this.validateForm = {}
    },

    // 点击编辑时触发
    editRow(index, row) {
		console.log(row)
      this.drawer = true
      this.drawerTitle = '编辑'
	  var rowObj = Object.assign({}, row) //
		this.validateForm = rowObj
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.ValidateForm = {}
      // 调用子组件里的 resetFields 方法
      // this.$refs.stockList.resetFields()
      // this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
		if (this.drawerTitle == '添加') {
		  // 新增工厂发货信息
		  this.addTableData()
		  this.drawer = false
		} else if (this.drawerTitle == '编辑') {
		  // 编辑工厂发货信息
		  this.editTableData()
		  this.drawer = false
		} 
		this.validateForm = {}
    },
	
	
	async addTableData(){
		const { data: res } = await this.$http.post(
		  '/shopUser/addShopUser',
		  this.validateForm
		)
		if (res.state == '200') {
		  this.$message({
		    message: res.msg,
		    type: 'success',
		    offset: 100,
		    center: true,
		    duration: 1000
		  })
		  this.initTableData()
		} else {
		  this.$message({
		    message: res.msg,
		    type: 'error',
		    offset: 100,
		    center: true,
		    duration: 3000
		  })
		}
	},
	
	async editTableData(){
		const { data: res } = await this.$http.post(
		  '/shopUser/updateShopUser',
		  this.validateForm
		)
		if (res.state == '200') {
		  this.$message({
		    message: res.msg,
		    type: 'success',
		    offset: 100,
		    center: true,
		    duration: 1000
		  })
		  this.initTableData()
		} else {
		  this.$message({
		    message: res.msg,
		    type: 'error',
		    offset: 100,
		    center: true,
		    duration: 3000
		  })
		}
	},

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData(true)
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData(true)
    },

	// 导出Excel表格
	exportExcel() {
		this.$confirm('确认导出Excel表格?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			})
			.then(async () => {
				this.downloadLoading = true
	
				await this.getExportTableData()
				this.downloadLoading = false
				// // 使用nextTick当table表格渲染完成后执行导出表格操作
				// this.$nextTick(() => {
				//   var xlsxParam = { raw: true } //转换成excel时，使用原始的格式
				//   /* 从表生成工作簿对象 */
				//   var wb = XLSX.utils.table_to_book(
				//     document.querySelector('#outTable'),
				//     xlsxParam
				//   )
				//   /* 获取二进制字符串作为输出 */
				//   var wbout = XLSX.write(wb, {
				//     bookType: 'xlsx',
				//     bookSST: true,
				//     type: 'array'
				//   })
				//   try {
				//     FileSaver.saveAs(
				//       //Blob 对象表示一个不可变、原始数据的类文件对象。
				//       //Blob 表示的不一定是JavaScript原生格式的数据。
				//       //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
				//       //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
				//       new Blob([wbout], {
				//         type: 'application/octet-stream'
				//       }),
				//       //设置导出文件名称
				//       "入库查询" + '.xlsx'
				//     )
				//   } catch (e) {
				//     if (typeof console !== 'undefined') console.log(e, wbout)
				//   }
				//   this.downloadLoading = false
				//   return wbout
				// })
			})
			.catch(() => {
				this.$message({
					type: 'info',
					message: '已取消导出Excel表格'
				})
			})
	},

    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    handleChange(file, fileList) {
      console.log(file)
      this.fileTemp = file.raw

      // 导入判断
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
          this.fileTemp.type == 'application/vnd.ms-excel'
        ) {
          this.importfxx(this.fileTemp)
        } else {
          this.$message({
            type: 'warning',
            message: '附件格式错误，请删除后重新上传！'
          })
        }
      } else {
        this.$message({
          type: 'warning',
          message: '请上传附件！'
        })
      }
    },

    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      )
    },

    // 文件列表移除文件时的钩子
    handleRemove(file, fileList) {
      this.fileTemp = null
    },

    // 导入格式
    importfxx(obj) {
      let _this = this
      // 通过DOM取文件数据
      this.file = obj
      var rABS = false //是否将文件读取为二进制字符串
      var f = this.file
      var reader = new FileReader()
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = ''
        var rABS = false //是否将文件读取为二进制字符串
        var pt = this
        var wb //读取完成的数据
        var outdata
        var reader = new FileReader()
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result)
          var length = bytes.byteLength
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i])
          }
          var XLSX = require('xlsx')
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: 'base64'
            })
          } else {
            wb = XLSX.read(binary, {
              type: 'binary'
            })
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]) //outdata就是你想要的东西
          console.log(outdata)
          this.da = [...outdata]
          let arr = []
          this.da.map((v) => {
            let obj = {
              robotCount: v['机器人数量'],
              type: v['发货进度'],
              applicantName: v['申请人'],
              factoryName: v['发货工厂'],
              consignorName: v['发货人'],
              receivingName: v['收货景区'],
              consigneeName: v['收货人'],
              consigneePhone: v['收货人电话'],
              receiningAddress: v['收货地址'],
              consignmentDate: v['发货时间']
            }
            arr.push(obj)
          })
          // 将 arr 赋值给 tableData，来更新表格数据
          this.tableData = arr
          return arr
        }
        reader.readAsArrayBuffer(f)
      }

      if (rABS) {
        reader.readAsArrayBuffer(f)
      } else {
        reader.readAsBinaryString(f)
      }
    },

    // 导入EXCEL文件超出限制时触发
    handleImportExceed(files) {
      this.$refs.uploadImport.clearFiles()
      // const file = files[0]
      this.$refs.uploadImport.submit(files)
    },

    // 导入EXCEL文件状态改变时触发
    handleImportChange(uploadFile, uploadFiles) {
      console.log(uploadFile)
      this.importE(uploadFile.raw)
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
    // StockList
  },
  computed: {
	  setTreasuretype(){
		  return (state) =>{
			  var result = ''
			  if (state == '1') {
			    result = '代金卷'
			  } else if (state == '2') {
			    result = '免单'
			  } else if(state == '3'){
			    result = '实物'
			  }else{
			  	result = '谢谢参与'
			  }
			  return result
		  }
	  },
	  setPrizeSize(){
		  return (state) =>{
		  			  var result = ''
		  			 if (state == '0') {
		  			   result = '普通奖品'
		  			 } else if (state == '1') {
		  			   result = '大奖'
		  			 } else {
		  			   result = '暂无'
		  			 }
		  			  return result
		  }
	  },
	  setProbability(){
		  return (state) =>{
				var result = state.split("-")
				console.log(result)
				if(result[1] == 9){
					return parseFloat(state).toFixed(9) + '%'
				}else if(result[1] == 8){
					return parseFloat(state).toFixed(8) + '%'
				}else if(result[1] == 7){
					return parseFloat(state).toFixed(7) + '%'
				}else if(result[1] == 6){
					return parseFloat(state).toFixed(6) + '%'
				}else if(result[1] == 5){
					return parseFloat(state).toFixed(5) + '%'
				}else if(result[1] == 4){
					return parseFloat(state).toFixed(4) + '%'
				}else {
					var arr = parseFloat(state).toFixed(10)
					return parseFloat(arr) + '%'
				}
		  }
	  },
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value);
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '待发货'
          } else if (row[value] == 2) {
            result = '已发货'
          } else if (row[value] == 3) {
            result = '已收货'
          } else if (row[value] == 4) {
            result = '已关闭'
          }
        } else if (value == 'inStockReason') {
          if (row[value] == 1) {
            result = '生产'
          } else if (row[value] == 2) {
            result = '销售'
          }
        } else if (value == 'inStockType') {
          if (row[value] == 1) {
            result = '采购'
          } else if (row[value] == 2) {
            result = '回收'
          }
        } else if(value == 'jackpotType'){
			if(row[value] == 1){
				 result = '定点寻宝'
			}else if(row[value] == 2){
				 result = '定时寻宝'
			}
		}else {
          result = row[value] || '无'
        }
        return result
      }
    }
  }
}
</script>

